@import url(https://fonts.googleapis.com/css2?family=Goldman:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #0158de;
}

/* APP COMPONENT */
.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.header h1 {
  color: #f3b251;
  font-size: 60px;
  text-align: center;
  font-family: 'Goldman', cursive;
}

.container {
  width: 1010px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-board {
  display: flex;
  align-items: center;
  margin: 5vh 0 7vh 0;
}

@media screen and (max-width: 950px) {
  
  .header {
    margin-bottom: 30px;
  }

  .header h1 {
    font-size: 40px;
  }

  .container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%
  }

  .container-board {
    flex-direction: column;
    margin: 0 auto;
    overflow-y: auto;
  }
}
.player-board {
  margin: 0 50px;
  width: 380px;
  height: 380px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1px;
  gap: 1px;
  /* background-color: #f3b2518a;
  border: 1px solid #f3b251; */
  background-color: #ffffffae;
  border: 1px solid #ffffff;
}

.pc-board {
  margin: 0 50px;
  width: 450px;
  height: 450px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 1px;
  gap: 1px;
  background-color: #ffffffae;
  border: 1px solid #ffffff;
  cursor: pointer;
}

.pc-board-none {
  display: none;
}

.grid-box {
  background-color: #0158de;
  display: flex;
  justify-content: center;
  align-items: center;
}

.grid-box:hover {
  background-color: #015ade94;
}

.grid-circle {
  border-radius: 50%;
  width: 50%;
  height: 50%;
}

.grid-circle-ship-player {
  background-color: #9aa3a8;
  border-radius: 0%;
  width: 100%;
  height: 100%;
}

.grid-circle-ship-pc {
  /* background-color: rgb(211, 60, 211);
  border-radius: 0%;
  width: 100%;
  height: 100%; */
  display: none;
}

.grid-circle-hit {
  background-color: #ff0000;
}

.grid-circle-miss {
  background-color: #fafafa;
}

@media screen and (max-width: 950px) {
  
  .player-board {
    width: 300px;
    height: 300px;
    margin-bottom: 50px;
  }

  .pc-board {
    width: 300px;
    height: 300px;
  }
}
/* BUTTONS */

.button-list {
  width: 240px;
  height: 70px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: #516bdd;
  -webkit-user-select: none; /* Chrome/Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+ */
  /* Rules below not implemented in browsers yet */
  -o-user-select: none;
  user-select: none;
}

.button-list-none {
  display: none;
}

.icon {
  width: 80px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.2s;
}

.icon:hover {
  background: #c98520;
}

.play-icon img {
  width: 50px;
  height: 50px;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.play-icon-invalid img {
  width: 50px;
  height: 50px;
  filter: invert(100%);
  -webkit-filter: invert(50%);
  cursor: auto;
}

.play-icon-invalid:hover {
  background-color: #516bdd;
}

.shuffle-icon img {
  width: 68px;
  height: 68px;
  overflow: hidden;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}

.trash-icon img {
  width: 30px;
  height: 30px;
  filter: invert(100%);
  -webkit-filter: invert(100%);
}
.modal-bg {
  background-color: rgba(0, 0, 0, 0.7);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  width: 40%;
  height: 40%;
  background-color: rgb(29, 47, 206);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Goldman', cursive;
}

.winning-message {
  color: #f3b251;
}

.play-again-button {
  font-family: 'Goldman', cursive;
  font-size: 18px;
  background-color: rgb(211, 26, 26);
  color: white;
  padding: 6px 12px;
  cursor: pointer;
  border: 1px solid rgba(255, 0, 0);
  border-radius: 5px;
  transition: 0.5s;
}

.play-again-button:hover {
  background-color: rgba(255, 0, 0, 0.726);
  border: 1px solid #ffd494;
}

.modal > * {
  margin: 5%;
}

.fade-in {
  animation: fadeIn ease 1s;
  -webkit-animation: fadeIn ease 1s;
  -moz-animation: fadeIn ease 1s;
  -o-animation: fadeIn ease 1s;
  -ms-animation: fadeIn ease 1s;
}

@keyframes fadeIn {
  0% {
      opacity:0;
  }
  100% {
      opacity:1;
  }
}

@media screen and (max-width: 950px) {
  
  .modal {
    text-align: center;
    width: 90%;
    height: 40%;
    justify-content: space-evenly;
  }
}
